import React from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { Ionicons } from '@expo/vector-icons';

// Define strict types for the show object with optional properties
interface ShowTimeInfoProps {
  show: {
    start_date?: string | null;
    end_date?: string | null;
    start_time?: string | null;
    end_time?: string | null;
    startTime?: string | null;
    endTime?: string | null;
    time?: string | null;
    description?: string | null;
    [key: string]: any; // Allow for additional properties
  };
}

// Shared regex: detects midnight-UTC ISO timestamps (treated as date-only)
const MIDNIGHT_UTC_REGEX =
  /^([0-9]{4})-([0-9]{2})-([0-9]{2})T0{2}:0{2}(?::0{2}(?:\.[0-9]{1,3})?)?(Z|[+\-]0{2}:?0{2})$/;

// InfoRow component for consistent "icon + text" rows
type InfoRowProps = {
  icon: React.ComponentProps<typeof Ionicons>['name'];
  label: string;
  value?: string | null;
};

/**
 * A robust InfoRow component that ensures all text is properly wrapped
 * in Text components and handles all edge cases.
 */
const InfoRow: React.FC<InfoRowProps> = ({ icon, label, value }) => {
  // Safe value with fallback
  const safeValue = value || 'Not specified';

  return (
    <View style={styles.infoRow}>
      <Ionicons name={icon} size={20} color="#666666" style={styles.infoIcon} />
      <View style={styles.infoContent}>
        <Text style={styles.infoLabel}>{label}</Text>
        <Text style={styles.infoValue}>{safeValue}</Text>
      </View>
    </View>
  );
};

/**
 * A super-robust ShowTimeInfo component that ensures all text is properly
 * wrapped in Text components and all data access is safely guarded.
 */
const ShowTimeInfo: React.FC<ShowTimeInfoProps> = ({ show }) => {
  // Ensure show object exists
  const safeShow = show || {};
  
  /**
   * Parse an incoming date string while preserving the intended **calendar day**
   * for plain `YYYY-MM-DD` inputs (avoid UTC-offset shifting that shows the
   * previous day).  Falls back to the built-in `Date` parser for full ISO
   * strings that include a time component.
   */
  const parseDatePreserveLocal = (date?: string | null): Date | null => {
    if (!date) return null;

    // Matches plain date strings like 2024-08-15
    const plainDateMatch = /^\\d{4}-\\d{2}-\\d{2}$/.exec(date);
    if (plainDateMatch) {
      const [y, m, d] = date.split('-').map(Number);
      return new Date(y, m - 1, d); // month is 0-based
    }

    /**
     * Treat **midnight-UTC** ISO timestamps as date-only strings.
     * Example matches:
     *   2024-08-15T00:00:00Z
     *   2024-08-15T00:00:00.000Z
     *   2024-08-15T00:00:00+00:00
     *   2024-08-15T00:00Z
     */
    const midnightMatch = MIDNIGHT_UTC_REGEX.exec(date);
    if (midnightMatch) {
      const [, yStr, mStr, dStr] = midnightMatch;
      const y = Number(yStr);
      const m = Number(mStr);
      const d = Number(dStr);
      return new Date(y, m - 1, d); // Interpret as local date
    }

    // Otherwise rely on normal parsing (may include timezone/offset)
    const parsed = new Date(date);
    return isNaN(parsed.getTime()) ? null : parsed;
  };

  // Format a date for display with comprehensive error handling
  const formatDate = (date?: string | null): string => {
    if (!date) return '';

    try {
      const dateObj = parseDatePreserveLocal(date);

      if (!dateObj) {
        if (__DEV__) console.warn(`[ShowTimeInfo] Invalid date format: ${date}`);
        return date;
      }

      return dateObj.toLocaleDateString('en-US', {
        weekday: 'long',
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      });
    } catch (e) {
      console.error('Error formatting date:', e);
      return date || ''; // Return original string or empty string
    }
  };

  // Check if dates are the same with robust error handling
  const areSameDates = (date1?: string | null, date2?: string | null): boolean => {
    if (!date1 || !date2) return false;
    
    try {
      const d1 = parseDatePreserveLocal(date1);
      const d2 = parseDatePreserveLocal(date2);

      if (!d1 || !d2) return false;
      
      return (
        d1.getFullYear() === d2.getFullYear() &&
        d1.getMonth() === d2.getMonth() &&
        d1.getDate() === d2.getDate()
      );
    } catch (e) {
      console.error('Error comparing dates:', e);
      return false;
    }
  };

  // Format date range for display with comprehensive error handling
  const formatDateRange = (): string => {
    // Support both DB snake_case fields and mapped camelCase fields
    const startDate = safeShow.start_date || safeShow.startDate;
    const endDate   = safeShow.end_date   || safeShow.endDate;
    
    if (!startDate) return 'Date not specified';
    
    // For single-day shows
    if (!endDate || areSameDates(startDate, endDate)) {
      return formatDate(startDate);
    }
    
    // For multi-day shows
    return `${formatDate(startDate)} to ${formatDate(endDate)}`;
  };

  // Format time with comprehensive error handling
  const formatTime = (timeString?: string | null): string => {
    if (!timeString) return '';
    
    try {
      // Try parsing as a full ISO date first
      let date;
      if (typeof timeString === 'string' && timeString.includes('T')) {
        date = new Date(timeString);
      } else {
        // If it's just a time string, add a dummy date
        date = new Date(`2000-01-01T${timeString}`);
      }
      
      // Check if date is valid
      if (isNaN(date.getTime())) {
        return timeString; // Return original string if parsing fails
      }
      
      return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    } catch (e) {
      console.error('Error formatting time:', e);
      return timeString; // Return original string if parsing fails
    }
  };

  // Get formatted show hours with comprehensive error handling
  const getFormattedShowHours = (): string => {
    // Try all possible time fields with safe access
    const startTime = safeShow.start_time || safeShow.startTime || safeShow.time;
    const endTime = safeShow.end_time || safeShow.endTime;
    
    // Format both times if available
    if (startTime && endTime) {
      const formattedStart = formatTime(startTime);
      const formattedEnd = formatTime(endTime);
      
      // Only show range if times are different
      if (formattedStart && formattedEnd && formattedStart !== formattedEnd) {
        return `${formattedStart} - ${formattedEnd}`;
      }
    }
    
    // Show single time if only start time is available
    if (startTime) {
      return formatTime(startTime);
    }
    
    // Show single time if only end time is available
    if (endTime) {
      return formatTime(endTime);
    }

    /* -------------------------------------------------------------
     * Fallback 2 – derive time component from ISO start/end dates
     * -------------------------------------------------------------
     * Some shows store the hour/minute directly in start_date /
     * end_date (full ISO timestamps) without separate start_time /
     * end_time columns.  If that’s the case – and the timestamp is
     * NOT a midnight-UTC sentinel (handled earlier by midnightUtcRegex)
     * – we can surface that time here.
     * ----------------------------------------------------------- */
    const startDateRaw = safeShow.start_date || safeShow.startDate;
    const endDateRaw   = safeShow.end_date   || safeShow.endDate;

    // Helper: ISO string has a usable time component (not midnight)
    const hasUsableTime = (iso?: string | null): boolean => {
      if (!iso || !iso.includes('T')) return false;
      // Re-use the same midnightUtcRegex from parseDatePreserveLocal
      // (scope-hoisted by JS so we can reference it here)
      return !MIDNIGHT_UTC_REGEX.test(iso);
    };

    const derivedStart = hasUsableTime(startDateRaw) ? startDateRaw! : undefined;
    const derivedEnd   = hasUsableTime(endDateRaw)   ? endDateRaw!   : undefined;

    if (derivedStart && derivedEnd) {
      const fs = formatTime(derivedStart);
      const fe = formatTime(derivedEnd);
      if (fs && fe && fs !== fe) return `${fs} - ${fe}`;
      return fs || fe;
    }

    if (derivedStart) return formatTime(derivedStart);
    if (derivedEnd)   return formatTime(derivedEnd);
    
    // Try to extract time from description as last resort
    const description = safeShow.description;
    if (description && typeof description === 'string') {
      // Look for common time patterns
      const timePattern = /(\d{1,2})(:\d{2})?\s*(am|pm)\s*[-–]?\s*(\d{1,2})(:\d{2})?\s*(am|pm)/i;
      const match = description.match(timePattern);
      
      if (match) {
        return `${match[1]}${match[2] || ''}${match[3]} - ${match[4]}${match[5] || ''}${match[6]}`;
      }
    }
    
    // Default fallback
    return 'Time not specified';
  };

  return (
    <View style={styles.container}>
      <Text style={styles.sectionTitle}>Show Details</Text>
      
      {/* Date row with calendar icon */}
      <InfoRow 
        icon="calendar" 
        label="Date"
        value={formatDateRange()} 
      />
      
      {/* Time row with time icon */}
      <InfoRow 
        icon="time" 
        label="Hours"
        value={getFormattedShowHours()} 
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    marginVertical: 10,
    padding: 15,
    backgroundColor: '#f8f8f8',
    borderRadius: 8,
  },
  sectionTitle: {
    fontSize: 18,
    fontWeight: 'bold',
    marginBottom: 12,
    color: '#333333',
  },
  infoRow: {
    flexDirection: 'row',
    marginBottom: 12,
  },
  infoIcon: {
    marginRight: 10,
    marginTop: 2,
  },
  infoContent: {
    flex: 1,
  },
  infoLabel: {
    fontSize: 14,
    color: '#666666',
    marginBottom: 2,
  },
  infoValue: {
    fontSize: 16,
    color: '#333333',
  }
});

export default ShowTimeInfo;
